.video__content {
  display: flex;
  justify-content: space-between;
}

.section__text.video__text {
  max-width: 50%;
}

@media screen and (max-width: 768px) {
  .video__content {
    flex-direction: column;
  }

  .section__text.video__text {
    min-width: 100%;
    margin: 0 0 40px 0;
  }
}